body {
  color: #333333;
}

.App {
  text-align: center;
}

.App-logo {
  max-width: 30vw;
  height: 20vmin;
  pointer-events: none;
}

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-title {
  flex: 1;
  color: #333333;
  font-weight: bold;
}

.App-subtitle {
  font-size: 18px;
  color: #333333;
}

.App-services ul {
  list-style-type: none;
}

.App-services li {
  list-style-type: none;
  text-align: left;
  color: #333333;
  margin: 10px;
  padding: 10px;
  background: #C8EEFB;
  font-size: 21px;
}

.App-link {
  color: #333333;
  font-size: 18px;
  margin: 8px;
}

.App-link a {
  color: #333333;
  text-decoration: none;
  background: #C8EEFB;
  padding: 4px;
  border-radius: 4px;
}

.App-group {
  flex: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.App-group-inline {
  flex: 2;
  display: flex;
  flex-direction: row;
  justify-content: center;
}